.plans {
  background: var(--bg-white);
  position: relative;
}

.plans-categories {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 3rem;
  flex-wrap: wrap;
}

.category-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 12px 24px;
  border: 2px solid var(--border-color);
  background: white;
  border-radius: 50px;
  font-weight: 500;
  color: var(--text-dark);
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 1rem;
}

.category-btn:hover {
  border-color: var(--primary-color);
  color: var(--primary-color);
  transform: translateY(-2px);
}

.category-btn.active {
  background: var(--primary-color);
  border-color: var(--primary-color);
  color: white;
  box-shadow: 0 4px 15px rgba(37, 99, 235, 0.3);
}

.category-btn i {
  font-size: 1.1rem;
}

.plans-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  margin-bottom: 4rem;
}

.plan-card {
  background: var(--bg-white);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  border: 2px solid var(--border-color);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.plan-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 8px 40px rgba(0, 0, 0, 0.15);
}

.plan-card.popular {
  border-color: var(--primary-color);
  box-shadow: 0 8px 40px rgba(37, 99, 235, 0.2);
  transform: scale(1.05);
}

.plan-card.popular:hover {
  transform: scale(1.05) translateY(-8px);
}

.popular-badge {
  position: absolute;
  top: -1px;
  left: -1px;
  right: -1px;
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  color: white;
  padding: 0.75rem;
  text-align: center;
  font-weight: 600;
  font-size: 0.9rem;
  border-radius: 18px 18px 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.plan-header {
  text-align: center;
  margin-bottom: 2rem;
  margin-top: 1rem;
}

.plan-icon {
  width: 60px;
  height: 60px;
  margin: 0 auto 1rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.5rem;
}

.plan-name {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-dark);
  margin-bottom: 0.5rem;
}

.plan-speed {
  font-size: 2rem;
  font-weight: 700;
  color: var(--primary-color);
  margin-bottom: 1rem;
}

.plan-pricing {
  text-align: center;
  margin-bottom: 2rem;
}

.plan-price {
  font-size: 3rem;
  font-weight: 700;
  color: var(--text-dark);
  line-height: 1;
}

.plan-period {
  font-size: 1.1rem;
  color: var(--text-light);
  margin-bottom: 0.5rem;
}

.plan-original-price {
  font-size: 1rem;
  color: var(--text-light);
  text-decoration: line-through;
}

.plan-features {
  margin-bottom: 2rem;
}

.plan-features ul {
  list-style: none;
  padding: 0;
}

.plan-features li {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem 0;
  border-bottom: 1px solid var(--border-color);
  color: var(--text-dark);
}

.plan-features li:last-child {
  border-bottom: none;
}

.plan-features li i {
  color: var(--success-color);
  font-size: 1rem;
  width: 16px;
}

.btn-full {
  width: 100%;
  justify-content: center;
  font-size: 1.1rem;
  font-weight: 600;
  padding: 1rem 2rem;
}

.plans-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 2rem;
  border-top: 1px solid var(--border-color);
  flex-wrap: wrap;
  gap: 2rem;
}

.payment-methods h4 {
  margin-bottom: 1rem;
  color: var(--text-dark);
  font-size: 1.1rem;
}

.payment-icons {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.payment-icons i {
  font-size: 2rem;
  color: var(--text-light);
}

.payment-icons span {
  font-weight: 600;
  color: var(--text-dark);
}

.plan-benefits {
  display: flex;
  gap: 2rem;
  flex-wrap: wrap;
}

.benefit-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--text-dark);
  font-weight: 500;
}

.benefit-item i {
  color: var(--success-color);
  font-size: 1.25rem;
}

/* Animations */
.plan-card {
  opacity: 0;
  transform: translateY(30px);
  animation: fadeInUp 0.6s ease forwards;
}

.plan-card:nth-child(1) { animation-delay: 0.1s; }
.plan-card:nth-child(2) { animation-delay: 0.2s; }
.plan-card:nth-child(3) { animation-delay: 0.3s; }
.plan-card:nth-child(4) { animation-delay: 0.4s; }

/* Plan category specific styles */
.plan-card[data-category="rural"] {
  border: 2px solid #059669;
}

.plan-card[data-category="empresarial"] {
  border: 2px solid #dc2626;
}

.plan-card[data-category="rural"] .popular-badge {
  background: linear-gradient(135deg, #059669, #047857);
}

.plan-card[data-category="empresarial"] .popular-badge {
  background: linear-gradient(135deg, #dc2626, #b91c1c);
}

/* Mobile Styles */
@media (max-width: 768px) {
  .plans-categories {
    gap: 0.75rem;
    margin-bottom: 2rem;
  }

  .category-btn {
    padding: 10px 20px;
    font-size: 0.9rem;
  }

  .plans-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .plan-card {
    padding: 1.5rem;
  }

  .plan-card.popular {
    transform: none;
  }

  .plan-card.popular:hover {
    transform: translateY(-8px);
  }

  .plan-price {
    font-size: 2.5rem;
  }

  .plan-speed {
    font-size: 1.5rem;
  }

  .plans-bottom {
    flex-direction: column;
    text-align: center;
  }

  .plan-benefits {
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .plan-card {
    padding: 1.25rem;
  }

  .plan-name {
    font-size: 1.25rem;
  }

  .plan-price {
    font-size: 2rem;
  }

  .plan-speed {
    font-size: 1.25rem;
  }

  .plan-benefits {
    flex-direction: column;
    gap: 1rem;
  }

  .payment-icons {
    justify-content: center;
  }
} 