.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  padding: 20px;
  animation: fadeIn 0.3s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.modal-content {
  background: white;
  border-radius: 20px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  max-width: 500px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  animation: slideIn 0.3s ease-out;
}

@keyframes slideIn {
  from {
    transform: translateY(-50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.modal-close {
  position: absolute;
  top: 20px;
  right: 20px;
  background: rgba(0, 0, 0, 0.1);
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 1.25rem;
  color: var(--text-light);
  transition: all 0.3s ease;
  z-index: 1;
}

.modal-close:hover {
  background: rgba(0, 0, 0, 0.2);
  color: var(--text-dark);
}

.modal-header {
  text-align: center;
  padding: 40px 40px 20px;
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  color: white;
  border-radius: 20px 20px 0 0;
  position: relative;
}

.modal-icon {
  width: 80px;
  height: 80px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  margin: 0 auto 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}

.modal-header h2 {
  font-size: 1.8rem;
  margin-bottom: 10px;
  font-weight: 700;
}

.modal-header p {
  font-size: 1rem;
  opacity: 0.9;
  line-height: 1.5;
}

.modal-form {
  padding: 30px 40px;
}

.modal-form .form-group {
  margin-bottom: 20px;
}

.modal-form label {
  display: block;
  margin-bottom: 8px;
  font-weight: 600;
  color: var(--text-dark);
  font-size: 0.9rem;
}

.modal-form input {
  width: 100%;
  padding: 12px 16px;
  border: 2px solid var(--border-color);
  border-radius: 10px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
  background: white;
}

.modal-form input:focus {
  outline: none;
  border-color: var(--primary-color);
}

.modal-form input:disabled {
  background: var(--bg-light);
  cursor: not-allowed;
}

.modal-form .btn {
  margin-top: 10px;
  padding: 12px 24px;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 10px;
}

.spinner {
  width: 16px;
  height: 16px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top: 2px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-right: 8px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.modal-result {
  padding: 30px 40px;
  text-align: center;
}

.result-icon {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  margin: 0 auto 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2.5rem;
  animation: bounceIn 0.5s ease-out;
}

@keyframes bounceIn {
  0% {
    transform: scale(0.3);
    opacity: 0;
  }
  50% {
    transform: scale(1.05);
  }
  70% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.result-icon.success {
  background: linear-gradient(135deg, var(--success-color), #059669);
  color: white;
}

.result-icon.warning {
  background: linear-gradient(135deg, var(--warning-color), #d97706);
  color: white;
}

.result-content h3 {
  font-size: 1.5rem;
  margin-bottom: 15px;
  color: var(--text-dark);
  font-weight: 700;
}

.result-content p {
  color: var(--text-light);
  line-height: 1.6;
  margin-bottom: 25px;
  font-size: 1rem;
}

.result-actions .btn {
  padding: 12px 24px;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 10px;
}

.modal-footer {
  padding: 20px 40px 30px;
  text-align: center;
  border-top: 1px solid var(--border-color);
  background: var(--bg-light);
  border-radius: 0 0 20px 20px;
}

.modal-footer p {
  color: var(--text-light);
  font-size: 0.85rem;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.modal-footer i {
  color: var(--success-color);
  font-size: 1rem;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .modal-overlay {
    padding: 15px;
  }

  .modal-content {
    max-width: 100%;
    border-radius: 15px;
  }

  .modal-header {
    padding: 30px 25px 20px;
    border-radius: 15px 15px 0 0;
  }

  .modal-header h2 {
    font-size: 1.5rem;
  }

  .modal-header p {
    font-size: 0.9rem;
  }

  .modal-form {
    padding: 25px;
  }

  .modal-result {
    padding: 25px;
  }

  .result-content h3 {
    font-size: 1.25rem;
  }

  .result-content p {
    font-size: 0.9rem;
  }

  .modal-footer {
    padding: 15px 25px 25px;
    border-radius: 0 0 15px 15px;
  }

  .modal-icon {
    width: 60px;
    height: 60px;
    font-size: 1.5rem;
  }

  .result-icon {
    width: 60px;
    height: 60px;
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .modal-overlay {
    padding: 10px;
  }

  .modal-header {
    padding: 25px 20px 15px;
  }

  .modal-form {
    padding: 20px;
  }

  .modal-result {
    padding: 20px;
  }

  .modal-footer {
    padding: 15px 20px 20px;
  }

  .modal-header h2 {
    font-size: 1.3rem;
  }

  .modal-header p {
    font-size: 0.85rem;
  }

  .modal-form input {
    padding: 10px 14px;
    font-size: 0.9rem;
  }

  .modal-form .btn {
    padding: 10px 20px;
    font-size: 0.9rem;
  }

  .result-actions .btn {
    padding: 10px 20px;
    font-size: 0.9rem;
  }
} 