.email-config-modal {
  background: white;
  border-radius: 20px;
  width: 100%;
  max-width: 800px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  animation: slideIn 0.3s ease-out;
}

.config-header {
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  color: white;
  padding: 30px;
  border-radius: 20px 20px 0 0;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.config-header h2 {
  font-size: 1.8rem;
  font-weight: 700;
  margin: 0;
  display: flex;
  align-items: center;
  gap: 12px;
}

.config-header i {
  font-size: 2rem;
}

.modal-close {
  background: rgba(255, 255, 255, 0.2);
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.25rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.modal-close:hover {
  background: rgba(255, 255, 255, 0.3);
}

.config-content {
  padding: 30px;
}

.config-section {
  margin-bottom: 40px;
}

.config-section:last-child {
  margin-bottom: 0;
}

.config-section h3 {
  font-size: 1.4rem;
  color: var(--text-dark);
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 12px;
  border-bottom: 2px solid var(--border-color);
  padding-bottom: 10px;
}

.config-section h3 i {
  color: var(--primary-color);
  font-size: 1.5rem;
}

.form-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  margin-bottom: 25px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  font-weight: 600;
  color: var(--text-dark);
  font-size: 0.95rem;
}

.form-group input {
  padding: 12px 16px;
  border: 2px solid var(--border-color);
  border-radius: 10px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
  background: white;
}

.form-group input:focus {
  outline: none;
  border-color: var(--primary-color);
}

.form-group small {
  color: var(--text-light);
  font-size: 0.85rem;
  margin-top: 4px;
}

.whatsapp-preview {
  background: var(--bg-light);
  padding: 10px 12px;
  border-radius: 8px;
  font-size: 0.9rem;
  color: var(--text-dark);
  margin-top: 8px;
}

.test-email-section {
  background: var(--bg-light);
  padding: 20px;
  border-radius: 12px;
  margin-top: 25px;
}

.test-email-section .btn {
  margin-bottom: 15px;
}

.test-result {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 12px 16px;
  border-radius: 8px;
  font-weight: 500;
  animation: fadeIn 0.3s ease-out;
}

.test-result.success {
  background: rgba(16, 185, 129, 0.1);
  color: var(--success-color);
  border: 1px solid rgba(16, 185, 129, 0.3);
}

.test-result.error {
  background: rgba(239, 68, 68, 0.1);
  color: var(--error-color);
  border: 1px solid rgba(239, 68, 68, 0.3);
}

.test-result i {
  font-size: 1.1rem;
}

.config-footer {
  padding: 20px 30px 30px;
  border-top: 1px solid var(--border-color);
  display: flex;
  justify-content: flex-end;
  gap: 15px;
  background: var(--bg-light);
  border-radius: 0 0 20px 20px;
}

.config-footer .btn {
  padding: 12px 24px;
  font-weight: 600;
  border-radius: 10px;
}

.spinner {
  width: 16px;
  height: 16px;
  border: 2px solid rgba(37, 99, 235, 0.3);
  border-top: 2px solid var(--primary-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-right: 8px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Mobile Styles */
@media (max-width: 768px) {
  .email-config-modal {
    margin: 10px;
    border-radius: 15px;
  }

  .config-header {
    padding: 25px 20px;
    border-radius: 15px 15px 0 0;
  }

  .config-header h2 {
    font-size: 1.5rem;
  }

  .config-content {
    padding: 25px 20px;
  }

  .form-grid {
    grid-template-columns: 1fr;
    gap: 15px;
  }

  .config-section {
    margin-bottom: 30px;
  }

  .config-section h3 {
    font-size: 1.25rem;
  }

  .config-footer {
    padding: 15px 20px 25px;
    border-radius: 0 0 15px 15px;
  }

  .config-footer .btn {
    flex: 1;
  }
}

@media (max-width: 480px) {
  .config-header {
    padding: 20px 15px;
    flex-direction: column;
    text-align: center;
    gap: 15px;
  }

  .config-header h2 {
    font-size: 1.3rem;
  }

  .config-content {
    padding: 20px 15px;
  }

  .test-email-section {
    padding: 15px;
  }

  .config-footer {
    padding: 15px;
    flex-direction: column;
  }

  .form-group input {
    padding: 10px 14px;
    font-size: 0.9rem;
  }
} 