.hero {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  overflow: hidden;
  padding: 120px 0 40px;
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, var(--primary-color) 0%, var(--secondary-color) 100%);
}

.hero-particles {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: 
    radial-gradient(circle at 25% 25%, rgba(255, 255, 255, 0.1) 2px, transparent 2px),
    radial-gradient(circle at 75% 75%, rgba(255, 255, 255, 0.1) 1px, transparent 1px);
  background-size: 100px 100px, 60px 60px;
  animation: float 20s ease-in-out infinite;
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-20px); }
}

.hero-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
  position: relative;
  z-index: 1;
}

.hero-text {
  color: white;
  padding-right: 2rem;
}

.hero-title {
  font-size: 3.5rem;
  font-weight: 700;
  line-height: 1.1;
  margin-bottom: 1.5rem;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.gradient-text {
  background: linear-gradient(45deg, #ffd700, #ffed4a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-shadow: none;
}

.hero-subtitle {
  font-size: 1.25rem;
  line-height: 1.6;
  margin-bottom: 2rem;
  opacity: 0.95;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}

.hero-features {
  display: flex;
  gap: 2rem;
  margin-bottom: 2.5rem;
  flex-wrap: wrap;
}

.feature-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: white;
  opacity: 1;
  background: rgba(0, 0, 0, 0.15);
  padding: 0.75rem 1rem;
  border-radius: 8px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.feature-item i {
  font-size: 1.25rem;
  color: #ffd700;
  filter: drop-shadow(0 1px 2px rgba(0, 0, 0, 0.3));
}

.hero-buttons {
  display: flex;
  gap: 1rem;
  margin-bottom: 3rem;
  flex-wrap: wrap;
}

.btn-large {
  padding: 16px 32px;
  font-size: 1.1rem;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
  font-weight: 600;
}

.btn-large:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
}

/* Estilos específicos para botões no Hero */
.hero .btn-primary {
  background-color: #ffd700;
  color: #dc2626;
  border: 2px solid #ffd700;
}

.hero .btn-primary:hover {
  background-color: #ffed4a;
  color: #b91c1c;
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(255, 215, 0, 0.4);
}

.hero .btn-outline {
  background-color: rgba(255, 255, 255, 0.95);
  color: #dc2626;
  border: 2px solid rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(10px);
}

.hero .btn-outline:hover {
  background-color: white;
  color: #b91c1c;
  border-color: white;
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(255, 255, 255, 0.3);
}

.hero .btn-outline i {
  color: #dc2626;
  font-size: 1.1rem;
}

.hero .btn-outline:hover i {
  color: #b91c1c;
}

.hero-stats {
  display: flex;
  gap: 3rem;
  flex-wrap: wrap;
}

.stat-item {
  text-align: center;
  color: white;
  background: rgba(0, 0, 0, 0.4);
  padding: 1.5rem 1rem;
  border-radius: 12px;
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.2);
  min-width: 120px;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  box-shadow: 0 6px 25px rgba(0, 0, 0, 0.3);
}

.stat-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #ffff00, #ffff80, #ffff00);
  opacity: 0.9;
  transition: all 0.3s ease;
}

.stat-item:hover::before {
  opacity: 1;
  height: 4px;
}

.stat-item:hover {
  transform: translateY(-5px);
  background: rgba(0, 0, 0, 0.5);
  border-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 10px 35px rgba(0, 0, 0, 0.4);
}

.stat-number {
  display: block;
  font-size: 2.5rem;
  font-weight: 800;
  color: #ffff00;
  margin-bottom: 0.5rem;
}

.stat-label {
  font-size: 0.9rem;
  opacity: 1;
  color: #f0f0f0;
  font-weight: 700;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

.hero-visual {
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-image {
  position: relative;
  width: 400px;
  height: 400px;
}

.connection-graphic {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.router-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80px;
  height: 80px;
  background: rgba(255, 255, 255, 0.15);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 3;
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.2);
}

.router-icon i {
  font-size: 2.5rem;
  color: #ffd700;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
}

.signal-waves {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.wave {
  position: absolute;
  border: 2px solid rgba(255, 215, 0, 0.4);
  border-radius: 50%;
  animation: pulse 2s ease-in-out infinite;
}

.wave-1 {
  width: 120px;
  height: 120px;
  top: -60px;
  left: -60px;
  animation-delay: 0s;
}

.wave-2 {
  width: 180px;
  height: 180px;
  top: -90px;
  left: -90px;
  animation-delay: 0.5s;
}

.wave-3 {
  width: 240px;
  height: 240px;
  top: -120px;
  left: -120px;
  animation-delay: 1s;
}

@keyframes pulse {
  0% {
    transform: scale(0.8);
    opacity: 1;
  }
  100% {
    transform: scale(1.2);
    opacity: 0;
  }
}

.connected-devices {
  position: absolute;
  width: 100%;
  height: 100%;
}

.device {
  position: absolute;
  width: 60px;
  height: 60px;
  background: rgba(255, 255, 255, 0.15);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: deviceFloat 3s ease-in-out infinite;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.device i {
  font-size: 1.5rem;
  color: white;
  filter: drop-shadow(0 1px 2px rgba(0, 0, 0, 0.3));
}

.device-1 {
  top: 20%;
  right: 10%;
  animation-delay: 0s;
}

.device-2 {
  bottom: 20%;
  left: 10%;
  animation-delay: 1s;
}

.device-3 {
  top: 60%;
  right: 20%;
  animation-delay: 2s;
}

.device-4 {
  top: 10%;
  left: 20%;
  animation-delay: 3s;
}

@keyframes deviceFloat {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

/* Mobile Styles */
@media (max-width: 768px) {
  .hero {
    padding: 100px 0 20px;
  }

  .hero-content {
    grid-template-columns: 1fr;
    gap: 2rem;
    text-align: center;
  }

  .hero-text {
    padding-right: 0;
  }

  .hero-title {
    font-size: 2.5rem;
  }

  .hero-subtitle {
    font-size: 1.1rem;
  }

  .hero-features {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }

  .feature-item {
    justify-content: center;
    width: fit-content;
  }

  .hero-buttons {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }

  .hero .btn-large {
    width: 100%;
    max-width: 280px;
    justify-content: center;
  }

  .hero-stats {
    justify-content: center;
    gap: 1.5rem;
  }

  .stat-item {
    min-width: 110px;
    padding: 1.25rem 1rem;
    background: rgba(0, 0, 0, 0.4);
    border: 2px solid rgba(255, 255, 255, 0.2);
  }

  .stat-number {
    font-size: 2.2rem;
  }

  .stat-label {
    font-size: 0.85rem;
    font-weight: 700;
  }

  .hero-image {
    width: 300px;
    height: 300px;
  }

  .wave-1, .wave-2, .wave-3 {
    width: 80px;
    height: 80px;
    top: -40px;
    left: -40px;
  }

  .device {
    width: 45px;
    height: 45px;
  }

  .device i {
    font-size: 1.2rem;
  }

  .device-1 {
    top: 15%;
    right: 5%;
  }

  .device-2 {
    bottom: 15%;
    left: 5%;
  }

  .device-3 {
    top: 70%;
    right: 15%;
  }

  .device-4 {
    top: 5%;
    left: 15%;
  }
}

@media (max-width: 480px) {
  .hero {
    padding: 90px 0 20px;
  }

  .hero-title {
    font-size: 2rem;
  }

  .hero-subtitle {
    font-size: 1rem;
  }

  .hero-stats {
    flex-direction: column;
    gap: 1rem;
    align-items: center;
  }

  .stat-item {
    width: 220px;
    padding: 1.5rem 1rem;
    background: rgba(0, 0, 0, 0.4);
    border: 2px solid rgba(255, 255, 255, 0.2);
  }

  .stat-number {
    font-size: 2rem;
  }

  .stat-label {
    font-size: 0.8rem;
    font-weight: 700;
  }

  .hero .btn-large {
    width: 100%;
    max-width: 250px;
    padding: 14px 24px;
    font-size: 1rem;
  }

  .hero-image {
    width: 250px;
    height: 250px;
  }

  .device {
    width: 40px;
    height: 40px;
  }

  .device i {
    font-size: 1rem;
  }

  .device-1 {
    top: 10%;
    right: 2%;
  }

  .device-2 {
    bottom: 10%;
    left: 2%;
  }

  .device-3 {
    top: 75%;
    right: 10%;
  }

  .device-4 {
    top: 2%;
    left: 10%;
  }

  .hero-features {
    gap: 0.75rem;
  }

  .feature-item {
    padding: 0.5rem 0.75rem;
    font-size: 0.9rem;
  }
} 