.testimonials {
  background: var(--bg-light);
  position: relative;
  overflow: hidden;
}

.testimonials::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100px;
  background: linear-gradient(to bottom, transparent, var(--bg-light));
  z-index: 1;
}

.testimonials-container {
  position: relative;
  z-index: 2;
}

.testimonial-card {
  background: var(--bg-white);
  border-radius: 20px;
  padding: 2.5rem;
  box-shadow: 0 8px 40px rgba(0, 0, 0, 0.1);
  border: 1px solid var(--border-color);
  max-width: 800px;
  margin: 0 auto 2rem;
  position: relative;
  transition: all 0.5s ease;
}

.testimonial-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 2rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.customer-info {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.customer-avatar {
  width: 60px;
  height: 60px;
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.5rem;
  flex-shrink: 0;
}

.customer-details h4 {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-dark);
  margin-bottom: 0.25rem;
}

.customer-location {
  color: var(--text-light);
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
}

.customer-rating {
  display: flex;
  gap: 0.25rem;
}

.customer-rating .fa-star {
  color: #e5e7eb;
  font-size: 0.875rem;
  transition: color 0.3s ease;
}

.customer-rating .fa-star.active {
  color: #fbbf24;
}

.testimonial-badge {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 0.5rem;
}

.plan-badge {
  background: var(--primary-color);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 500;
}

.date-badge {
  color: var(--text-light);
  font-size: 0.8rem;
}

.testimonial-content {
  position: relative;
}

.quote-icon {
  position: absolute;
  top: -10px;
  left: -10px;
  color: var(--primary-color);
  font-size: 2rem;
  opacity: 0.3;
}

.testimonial-text {
  font-size: 1.1rem;
  line-height: 1.7;
  color: var(--text-dark);
  font-style: italic;
  margin-left: 1rem;
  position: relative;
  z-index: 2;
}

.testimonials-navigation {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin-bottom: 3rem;
}

.nav-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #d1d5db;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.nav-dot.active {
  background: var(--primary-color);
  transform: scale(1.2);
}

.nav-dot:hover {
  background: var(--accent-color);
}

.testimonials-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
}

.stat-item {
  text-align: center;
  padding: 2rem 1.5rem;
  background: var(--bg-white);
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  border: 1px solid var(--border-color);
  transition: transform 0.3s ease;
}

.stat-item:hover {
  transform: translateY(-5px);
}

.stat-number {
  font-size: 2.5rem;
  font-weight: 800;
  color: var(--primary-color);
  margin-bottom: 0.5rem;
}

.stat-label {
  color: var(--text-dark);
  font-weight: 500;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
}

.stat-stars {
  display: flex;
  gap: 0.25rem;
}

.stat-stars .fa-star {
  color: #fbbf24;
  font-size: 1rem;
}

/* Animações */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.testimonial-card {
  animation: fadeIn 0.5s ease-out;
}

/* Responsividade */
@media (max-width: 768px) {
  .testimonial-card {
    padding: 2rem 1.5rem;
    margin: 0 auto 1.5rem;
  }

  .testimonial-header {
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
  }

  .testimonial-badge {
    align-items: flex-start;
    width: 100%;
  }

  .customer-info {
    width: 100%;
  }

  .customer-avatar {
    width: 50px;
    height: 50px;
    font-size: 1.25rem;
  }

  .customer-details h4 {
    font-size: 1.1rem;
  }

  .testimonial-text {
    font-size: 1rem;
    margin-left: 0.5rem;
  }

  .quote-icon {
    font-size: 1.5rem;
    top: -5px;
    left: -5px;
  }

  .testimonials-stats {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }

  .stat-item {
    padding: 1.5rem 1rem;
  }

  .stat-number {
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .testimonial-card {
    padding: 1.5rem 1rem;
    border-radius: 15px;
  }

  .customer-info {
    flex-direction: column;
    text-align: center;
    gap: 1rem;
  }

  .customer-avatar {
    width: 60px;
    height: 60px;
    font-size: 1.5rem;
  }

  .testimonial-text {
    font-size: 0.95rem;
    margin-left: 0;
  }

  .testimonials-stats {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .stat-item {
    padding: 1.25rem 0.75rem;
  }

  .stat-number {
    font-size: 1.75rem;
  }

  .plan-badge {
    font-size: 0.75rem;
    padding: 0.4rem 0.8rem;
  }
}

/* Acessibilidade */
@media (prefers-reduced-motion: reduce) {
  .testimonial-card {
    animation: none;
  }
  
  .stat-item:hover {
    transform: none;
  }
  
  .nav-dot:hover {
    transform: none;
  }
} 